// Helper to get the correct path with basePath for static export
// Detect if we're in ComfyUI mode by checking build mode or URL path
function getBasePath(): string {
  // Check if we're building for ComfyUI (static export)
  // This environment variable is set during build time
  if (process.env.BUILD_MODE === "comfyui") {
    return "/opencut";
  }
  
  // Runtime detection for client-side
  if (typeof window !== "undefined") {
    // Check if we're running under /opencut path (ComfyUI mode)
    if (window.location.pathname.startsWith("/opencut")) {
      return "/opencut";
    }
  }
  
  // Default to no base path (dev mode or regular deployment)
  return "";
}

export const basePath = getBasePath();

export function withBasePath(path: string): string {
  // For build time, use the BUILD_MODE environment variable
  const buildBasePath = process.env.BUILD_MODE === "comfyui" ? "/opencut" : "";
  
  // Use buildBasePath if available, otherwise use runtime basePath
  const effectiveBasePath = buildBasePath || basePath;
  
  // If no basePath, return path as-is
  if (!effectiveBasePath) {
    return path;
  }
  
  if (path.startsWith("/")) {
    return `${effectiveBasePath}${path}`;
  }
  return `${effectiveBasePath}/${path}`;
}